import React, { Component } from 'react';
import {Link} from 'react-router-dom';
import axios from 'axios';
import {url_categories} from '../../../urls'
import ContentWrapper from '../Layout/ContentWrapper';
import HeadingMenu from '../Layout/HeadingMenu';
import { Row, Col, Card, CardHeader, CardBody, Button } from 'reactstrap';
import ApercuService from './ApercuService';
import swal from 'sweetalert';

class ListeService extends Component {
    constructor() {
        super()
        this.state = {
            defaultServices: [],
            services: [],
            isLoading: true,
            path: [
                { 
                    label: 'Home',
                    value: '/dashboard_client',
                },
                { 
                    label: 'Liste des services',
                    value: '',
                }
            ]
        }
    }

    getService = () => {
        let services = [];
        axios.get(url_categories)
        .then(res => {
            const response = res.data.data
            console.log(res.data.data)
            response.map((categorie) => {
                const {key, nom, description, logo, tickets, partenaires, sommeBte} = categorie
                //nombre de partenaires
                const nb_partenaire = partenaires.length
                //nombre de ticket en stock
                let initialValueT = 0
                let total_ticket = tickets.reduce(function (previousValue, currentValue) {
                    return previousValue + currentValue.qte
                }, initialValueT)
                // nombre de bouteilles déjà recyclés grâce à ce service
                const nb_bte = sommeBte

                services.push({
                    key,
                    nom,
                    description,
                    logo,
                    partenaires,
                    tickets,
                    nb_partenaire,
                    nb_ticket : total_ticket,
                    nb_bte,
                })
            })
            console.log(services);
            this.setState({
                defaultServices: services,
                services: services, 
                isLoading: false,
            })
            console.log(this.state.defaultServices);
        })
        .catch((error) => {
            console.log("get services failed!", error)
            swal('Erreur', error.message, 'error')
        })
    }

    componentDidMount() {
       this.getService();
    }

    render() {
       const services = this.state.defaultServices
       const isLoading = this.state.isLoading
       /*const services = [
           {
               key: "test001",
               nom: "Carburant",
               description: "Faites le plein en energie verte!",
               logo: "img/user/02.jpg",
               partenaires: [{
                   key: "pat001",
                   nom: "Orange Cameroun",
                   description: "La vie change avec Orange",
                   logo: "orange.png"
               }],
               tickets: [{
                   id_ticket: '1',
                   prix: 3,
                   quantite: 5,
               }],
               nb_partenaire: 1,
               nb_ticket: 1,
               nb_bte: 1500 
           }
       ]*/
        return (
            !isLoading? (
            <ContentWrapper>
                <HeadingMenu path = {this.state.path}/>
                <Row>
                    <div className="col-xl-3 ml-auto">
                            {/*add icon */}
                            <Link to={`/add_service/`} title="Ajouter un service" className="d-flex col-xl-7 ml-auto pr-3">
                                <Button color="success"> Ajouter </Button>
                            </Link>
                    </div>
                </Row>
                <Row className="mr-15">
                    <Col>
                        <Row>
                            {services.map((service) => {
                                return (
                                    <Col xl="2" lg="6">
                                        <ApercuService service = {service}/>
                                    </Col>
                                )
                            })}
                        </Row>
                    </Col>
                </Row>
            </ContentWrapper>
            ):(
                <div> En cours de chargement... </div>
            ));
    }

}

export default ListeService;
