import React, { Component } from 'react';
import {Link} from 'react-router-dom'
import { Row, Col, Card, CardHeader, CardBody } from 'reactstrap';

class ApercuService extends Component {
    
    render() {
        const key = this.props.service.key
        const nom = this.props.service.nom
        const description = this.props.service.description
        const logo = this.props.service.logo
        const nb_partenaire = this.props.service.nb_partenaire // nombre de partenaire abonné à ce service
        const nb_ticket = this.props.service.nb_ticket // nombre de tickets en stock
        const nb_bte = this.props.service.nb_bte // nombre de bouteilles que ce service a permit de recycler
        
        return (
            <Card className="b text-center">
                <CardBody>
                    <img className="rounded-circle thumb64 mb-2" src={logo} alt="User"/>
                    <p className="h4 text-bold mb-0">{nom}</p>
                    <p>{description}</p>
                    <br/>
                    <Link to={`/show_service/${key}`} className="btn btn-info btn-oval" type="button"> 
                        Consulter 
                    </Link>
                </CardBody>
                <CardBody className="bt">
                    <Row>
                        <Col col="4" className="br">
                            <em className="fas fa-user-tie fa-fw text-primary mr-3"></em>
                            <strong>+{nb_partenaire} </strong>
                        </Col>
                        <Col col="4" className="br">
                            <em className="fas fa-ticket-alt fa-fw text-warning mr-3"></em>
                            <strong>+{nb_ticket} </strong>
                        </Col>
                        <Col xs="4">
                            <em className="fas fa-recycle fa-fw text-success mr-3"></em>
                            <strong>+{nb_bte} </strong>
                        </Col>
                    </Row>
                </CardBody>
            </Card>
     
        );
    }

}

export default ApercuService;
